    time_month: function time_month() {
      var today = new Date();
      var today_year = today.getFullYear();
      var today_mon = today.getMonth() + 1;
      var today_day = today.getDate();
      var today_month = today_year + "-" + (today_mon < 10 ? "0" + today_mon : today_mon) + "-" + (today_day < 10 ? "0" + today_day : today_day);
      var tommonr = new Date();
      tommonr = +tommonr + 1000 * 60 * 60 * 24 * 1;
      tommonr = new Date(tommonr);
      var tommonr_year = tommonr.getFullYear();
      var tommonr_mon = tommonr.getMonth() + 1;
      var tommonr_day = tommonr.getDate();
      var tommonr_month = tommonr_year + "-" + (tommonr_mon < 10 ? "0" + tommonr_mon : tommonr_mon) + "-" + (tommonr_day < 10 ? "0" + tommonr_day : tommonr_day);
      var yestoday = new Date();
      yestoday = +yestoday - 1000 * 60 * 60 * 24 * 1;
      yestoday = new Date(yestoday);
      var yestoday_year = yestoday.getFullYear();
      var yestoday_mon = yestoday.getMonth() + 1;
      var yestoday_day = yestoday.getDate();
      var yestoday_month = yestoday_year + "-" + (yestoday_mon < 10 ? "0" + yestoday_mon : yestoday_mon) + "-" + (yestoday_day < 10 ? "0" + yestoday_day : yestoday_day);

      var timeData = [];
      timeData[0] = today_month;
      timeData[1] = tommonr_month;
      timeData[2] = yestoday_month;
      return timeData;
    },

